body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
  border: 0px;
  margin: 0px;
  padding: 0px;
}


.home-container {
  height:100vh;
  background: #161616;
}

.home-container--black {
  background: #2b2b29;
}
.colour-button {
  cursor: pointer;
  width: 100%;
  height: 50px;
  margin: 10px 0;
  box-sizing: border-box; 
}

.colour-button__colour--blue {
  background: #00bee0;
}

.colour-button__colour--green {
  background: #00e49c;
}

.colour-button__colour--yellow {
  background: #F0CD13;
}

.colour-button__colour--red {
  background: #ff696b;
}

.colour-button__colour--black {
  background: #000000;
}

.colour-button__colour--blue.colour-button--selected,
.colour-button__colour--green.colour-button--selected,
.colour-button__colour--yellow.colour-button--selected,
.colour-button__colour--red.colour-button--selected {
  border: solid 2px #000000;
}

.colour-button__colour--black.colour-button--selected {
  border: solid 2px #555555;
}
.colour-selector {
  position: absolute;
  right: -50px; 
  height: 100%;
  width: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  transition: right .3s;
}

.colour-selector--enabled {
  right: 20px;
}

.colour-selector--container {
  width: 100%;
}

.animate-stand {
  position: absolute;
  bottom: 10px;
  left: 10px;
}
.designer {
  height:100vh;
  background: #2b2b29;
  position: relative;
}

.hero-video {
  width: 100%;
  height: 100%; 
  position: relative; 
  overflow: hidden;
}

.hero-video__iframe {
  border: none;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  padding: 0;
  margin: 0;
}


.parallax-panel {
  height:100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.parallax-panel--container {
  width: 100%;
  height: 100%;
}

.parallax-image {
  width: 100%;
  height: 100vh;
}

.parallax-image--img {
  position: absolute;
}
.map {
  height:50vh;
  width: 100%;
  left: 0;
}

.mapboxgl-canvas {
  left: 0;
}
